<template>
  <div class="largeContain">
    <div>
      <span class="title">{{$t('title')}}</span>
    </div>
    <!--Menu to select citation of what convention is presented-->
    <div class="conventionContain">
      {{$t('prompt')}}
      <el-select v-model="presentConvention" class="conventionField"> <!--pull down menu to select presented citation convention-->
        <el-option v-for="item in avaliableConventions" :key="item.value" :label="$t(item.label)" :value="item.value">
        </el-option>
      </el-select>
    </div>
    <!--Display generated citations-->
    <div class="resultContain">
      <div v-show="citationList.length==0" style="font-family: Roboto;font-style: normal; color: rgb(93,95,99);"> <!--Display div block if no citations are created-->
        {{$t('nullPrompt')}}
      </div>
      <div class="citationContain" v-for="(item,index) in this.$store.getters.getCitations"
       :key="index" @mouseover="mouseHover(index)" @mouseout="curIndex=-1"> <!--Display generated citations using for loop, each citation is binded to an index-->
        <span class="citationText">{{index+1}}. {{item}}</span>
        <div class="mask" v-show="index == curIndex"></div>  
        <div class="floatContain" v-show="index == curIndex"> <!--Display copy citation and delete citation button on top of the citation if cursor hovers over the current citation-->
            <el-button class="floatButton" @click="copyCitation(index)">{{$t('copy')}}</el-button>
            <el-button class="floatButton" @click="deleteCitation(index)">{{$t('delete')}}</el-button>
            <el-button class="floatButton" @click="edit(index)" v-show="false">{{$t('edit')}}</el-button>
        </div>
      </div>
    </div>
    <!--buttons to create another citation, copy citations, and export citation-->
    <div class="buttonContain">
      <el-button rounded class="buttons" @click="directToMain()">
          {{$t('another')}}
      </el-button>
      <el-button rounded class="buttons" @click="copyeAll()">
          {{$t('copyAll')}}
      </el-button>
      <el-button rounded class="buttons" @click="handleCommand()">
          {{$t('export')}}
      </el-button>
    </div>
  </div>
</template>

<script>
  export default{
    name: 'vcitation',
    data(){
      return {
        citationList: this.$store.getters.getCitations, //initialize list to store created citations
        curIndex: -1, //Initialize cursor's current position (set to -1 because cursor is not hovering over any citation)
        avaliableConventions: this.$store.getters.getAvalConv, //initialize variable to store available conventions
        presentConvention: this.$store.getters.getConvention, //currently seletected convention
      }
    },
    methods:{
      mouseHover(index){ //handle mouseHover event evoked whenever the cursor move over a citation
        this.curIndex = index //set the curIndex to the index of the citation that the cursor is hovering over (L22, 23 will react to change in curIndex and display the copy citation and delete citation buttons)
      },
      directToMain(){ //Direct to webpage 1 when "Create Another Citation" is clicked
        this.$store.commit('changeShowSelect',true)
        this.$router.push({name: 'main'}) 
      },
      copyCitation(index){ //Copy citation when Copy Citation button clicked
        var selectedCitation = this.citationList[index] 
        this.$copyText(selectedCitation).then(()=>{ //.then clause used to handle success and fail callback
          this.$message({ //Notify user if copy successed
            message:"Citation Copied",
            type: 'success'
          })
        },()=>{
          this.$message({ //Copy user if copy failed
            message: "Failed to copy citation, please try again!",
            type: 'error'
          })
        })
      },
      copyeAll(){ //Similar logic as above; function to copy all citation when "copy all citations" clicked
        if(this.citationList.length==0){
          this.$message({
            message: "There are currently no citation created. Click 'Create Another Citation' to create a citation.",
            type: 'warning'
          })
        }
        var res = ""
        for(let i = 0; i < this.citationList.length; i++){
          res = res + "[" + String(i+1) + "]  " + this.citationList[i] + "\n"
        } //integrate all created citations from a list into a single string 
        this.$copyText(res).then(()=>{
          this.$message({
            message:"Citation Copied",
            type: 'success'
          })
        },()=>{
          this.$message({
            message: "Failed to copy citation, please copy citation by hand!",
            type: 'error'
          })
        })
      },
      deleteCitation(index){ //Delete specified citation from the central store 
        this.$store.commit('deleteCitation',index)
        this.citationList = this.$store.getters.getCitations
      },
      handleCommand(){ 
        if(this.citationList.length == 0){ //Warn user when "Export citation " clicked by user but no citations are created
          this.$message({
            message: "No citations to export, please click 'Create Another Citation' to create a citation.",
            type: 'warning'
          })
        } else {
          let text = 'Work Cited List'//Initialize variable to contain texts of the txt file
          for (let i = 0; i < this.citationList.length; i++) {
            text = text + '\n' + '[' + String(i+1) + '] ' + this.citationList[i]
          } //Adding generated citations to text
          let downloadFile = (filePath, fileName) =>{
            let a = document.createElement('a') //Create an hyperlink tag
            a.href = filePath //Set url of the target that will be downloaded
            a.download = fileName //Specify target that will be downloaded when the tag is clicked
            document.body.appendChild(a) //Append tag to the DOM 
            a.click() //Manually trigger the click event
            document.body.removeChild(a) //Remove tag from the DOM 
          }
          let blob = new Blob([text],{type: "text/plain"}) //Initialize Blob with text and specify type as a txt file
          let url = URL.createObjectURL(blob) //Create objectURL for the txt file
          downloadFile(url,"Work Cited List.txt") //Evoke function defined above
          URL.revokeObjectURL(url) //Release object url as it is no longer required
        }
      },
      edit(idx){
        this.$store.commit('setEditCitation',idx)
        this.$router.push({name: 'manual'})
      }
    },
    watch: {
      "presentConvention": function(val){ //Monitor the value of presentConvention, executes below code whenever the value changes
         this.$store.commit('selectConvention',val) //Change the selected convention
         this.citationList = this.$store.getters.getCitations //Update the presented citations list according to the convention selected
      }
    },
    i18n: {
      messages:{
        en: {
          title: 'My Citation',
          prompt: 'Displaying citation created in form:',
          'National Standard': 'National Standard',
          'Campus Simplified Version': 'Campus Simplified Version',
          another: 'Create Another Citation',
          copyAll: 'Copy All Citations',
          'export': 'Export Citations',
          'nullPrompt': 'There are currently no citation created. Click "Create Another Citation" to create a citation.',
          'copy': 'Copy',
          'delete': 'Delete',
          'edit': 'Edit'
        },
        cn: {
          title: '',
          prompt: ':',
          'National Standard': '',
          'Campus Simplified Version': '',
          another: '',
          copyAll: '',
          'export': '',
          'nullPrompt': '',
          'copy': '',
          'delete': '',
          'edit': ''
        }
      }
    }
  }
</script>

