<template>
  <div class="bigContain">
    <vpopup :isShow="showPopUp" @popUpClosed="closePopUp"></vpopup>
    <div class="contain">
      <div class="popUpContain">
        <button @click="clickedPopUp" class="popUpBotton">
          <img src="./popUpButton.svg" />
        </button>
      </div>
      <div class="titleContain">
        <!--Title of the website-->
        <span class="title">{{$t('title')}}</span>
      </div>
      <div class="menuContain">
        <div class="menuTitleContain">
          <span class="menuTitle">{{$t('prompt')}}</span>
        </div>
        <div class="menuButtonContain">
          <div class="button1"  v-show="false">
            <!--Direct to webpage 2 when <el-button> is pressed-->
            <router-link :to="{name: 'default'}">
              <el-button round class="button" @click="clicked('KS')">
                <span class="text">{{$t('convention1')}}</span>
              </el-button>
            </router-link>
          </div>
          <div class="button2">
            <!--Direct to webpage 2 when <el-button> is pressed-->
            <router-link :to="{name: 'default'}"> 
              <el-button round class="button" @click="clicked('nationalStandard')">
                <span class="text">{{$t('convention2')}}</span>
              </el-button>
            </router-link>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import vpopup from './vpopup.vue' //Import pop-up menu component
  export default {
    name: "v-main", 
    components:{
      vpopup //Register pop-up menu component
    },
    data() {
      return {
        showPopUp: false, //Initialize variable setting visbility of the pop-up menu
        type: ""
      }
    },
    methods:{
      clickedPopUp(){
        this.showPopUp = !this.showPopUp //Reverse visibility state when pop-up menu button is clicked
      },
      closePopUp(state){
        this.showPopUp = state //Handle event emitted from pop-up menu component and set visbility to passed data (state)
      },
      clicked(type){
        this.$store.commit('selectConvention',type)
      }
    },
    i18n: {
      messages: {
        en: {
          title: 'Chinese Citation Generator',
          prompt: 'Please select type of citation:',
          convention1: 'Campus Simplified Version',
          convention2: 'National Standard Version'
        },
        cn: {
          title: '',
          prompt: ':',
          convention1: '',
          convention2: ''
        }
      }
    }
  }
</script>

