import Vue from 'vue'
import Router from 'vue-router'
import vmain from '../components/vmain.vue'
import vtypepage from '../components/typePage.vue'
import vsource from '../components/sourceSearch.vue'
import vmanual from '../components/manual.vue'
import vcitation from '../components/citation.vue'

Vue.use(Router)
export default new Router({ 
    mode: 'history',
    routes: [ 
      {
        path: '/chinese-citation-generator/',
        name: 'main',
        component: vmain
      },
      {
        path: '/chinese-citation-generator/type',
        name: 'type',
        component: vtypepage,
        children: [ //Define children children components of vtypepage
          {path: '', name: 'default', component: vsource},
          {path: '/manual', name: 'manual', component: vmanual},
          {path: '/citation', name: 'citation', component: vcitation}
      ]
      }
    ]
  })