<template>
  <div class="bigContain">
    <!--Implement pop-up mnu-->
    <vpopup :isShow="showPopUp" @popUpClosed="closePopUp"></vpopup>
    <div class="contain">
      <div class="popUpContain">
        <button @click="clickedPopUp" class="popUpBotton">
          <img src="./popUpButton.svg" />
        </button>
      </div>
      <div class="titleContain">
        <!--Website Title-->
        <span class="title">{{$t('title')}}</span>
      </div>
      <div class="menuContain">
        <!--Router view renders child component when directToPage even is emitted from child component-->
        <router-view @directToPage="directToPage"></router-view>
      </div>
    </div>
  </div>
</template>

<script>
  //import component
  import vpopup from './vpopup.vue'
  export default{
    name: "v-type-page",
    components:{
      vpopup //Register popup menu component
    },
    data(){
      return {
        type: this.$store.getters.getConvention,
        showPopUp: false, //Data passed to vpopup as props
        chosenSource: "",
      }
    },
    methods:{
      clickedPopUp(){ 
        this.showPopUp = !this.showPopUp 
        //Mutate the value of showPopUp when the pop up button is clicked
      },
      closePopUp(state){ //Handler function to receive and process data from vpopup
        this.showPopUp = state
      },
      directToPage(name){ //Handle directToPage event emitted form child component (some interactive element in child component is provoked to jump to another page)
        console.log(name)
        this.$router.push({name: name}) //Specify path with router
      }
    },
    i18n: {
      messages: {
        en: {
          title: 'Chinese Citation Generator',
          prompt: 'Please select type of source to cite:',
        },
        cn: {
          title: '',
          prompt: ':',
        }
      }
    }
  }
</script>

