<template>
  <div class="Contain">
    <div class="menuTitleContain">
      <span class="menuTitle">{{$t('prompt')}}</span>
    </div>
    <div class="menuButtonContain"> <!--3 Buttons to display available source types-->
      <div v-for="(source,idx) in typeList" class="buttonContain" :key="idx">
        <el-button v-if="idx<3" round class="buttonText" @click="sourceButtonClicked(source)">
          <span>{{$t(source)}}</span>
        </el-button>
      </div>
      <!--Dropdown menu to display other avilable source types when number of source type -->
      <div class="dropDownContain">
        <el-dropdown placement="bottom" class="dropdown" trigger="click">
          <el-button round class="dropDownButton">
            <span>{{$t('More')}}</span>
            <img src="./arrow.svg" />
          </el-button>
          <el-dropdown-menu class="dropDownMenu">
              <el-dropdown-item v-for="(source,idx) in typeList"  :key="idx" class="dropDownItem">
                  <span v-if="idx>=3" @click="sourceButtonClicked(source)">{{$t(source)}}</span>
              </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>

<script>
  export default{
    name: "vsource",
    data() {
      return {
        typeList : this.$store.getters.getType //initialize variable to store available source types
      };
    },
    methods:{
      sourceButtonClicked(source){ //Called when any button/pull down menu containing a source type is clicked
        this.$store.commit("selectType",source) //Update selected source type to central store
        this.$emit('directToPage','manual') //Emite directToPage event to parent component (typePage.vue) with parameter 'manual' (redirect to compoentn with name 'manual')
      }
    },
    i18n: {
      messages: {
        en: {
          title: 'Chinese Citation Generator',
          prompt: 'Please select type of citation:',
          'Monograph': 'Monograph',
          'Contribution Literature': 'Contribution Literature',
          'Serial': 'Serial',
          'Electronic Resource': 'Electronic Resource',
          'Patent Literature': 'Patent Literature',
          'More': 'More'
        },
        cn: {
          title: '',
          prompt: ':',
          'Monograph': '',
          'Contribution Literature': '',
          'Serial': '',
          'Electronic Resource': '',
          'Patent Literature': '',
          'More': ''
        }
      }
    }
  }
</script>

