<template>
   <el-drawer :with-header="false" :visible.sync="state" :direction="direction" :before-close="handleClose" class="bigContain">
     <div class="contain">
       <div class="navButContain">
         <button class="navBut" @click="clickPopUp">
           <img src='./popUpButton.svg'/>
         </button>
       </div>
       <div class="creatCitontain">
         <span class="text" @click="directToMain">{{$t('create')}}</span>
       </div>
       <div class="myCitContain">
         <span class="text" @click="directToCitation">{{$t('my')}}</span>
       </div>
       <div class="switchLangContain">
         <span class="text" @click="switchLanguage">{{$t('switch')}}</span>
       </div>
     </div>
   </el-drawer>
</template>

<script>
  export default {
    name: "vpopup", //Name of Component
    data() { //State of the component
      return {
        direction: "ltr",
        state: false
      }
    },
    props: ["isShow"], //Declare props to receive external value passed by parent components 
    methods:{ //Define methods 
      handleClose(){
        this.$emit("popUpClosed",false); //Passing data to parent component through $emit with a boolean parameter
      },
      clickPopUp(){
        this.$emit("popUpClosed",false); //Passing data to parent component through $emit with a boolean parameter
      },
      directToMain(){//Function to direct to webpage 1
        let currentPage = this.$store.getters.getCurrentPage //Get name of current webpage from central store
        console.log(currentPage)
        if(currentPage.match("manual")){ //If the current page is webpage 3 (user is filling in information)
          //Present dialogue box warning user if jumping to other webpage will lose current information
          this.$confirm("Are you sure to redirect to another page? Your information will not be preserved.", "Page Redirection", {
            confirmButtonText: "Yes",
            cancelButtonText: "Cancel",
            type: "warning"
          }).then(()=>{ //User selected Yes (jump to webpage 1)
            this.$store.commit("setEmptyCitationFormat", this.$store.getters.getFormat)
            this.$store.commit('changeShowSelect',true)
            this.$router.push({name: 'main'})
          }).catch(()=>{ //User selected Cancel (stay on current page)
            this.$message({
              type: 'info',
              message: 'Redirection canceled.'
            })
          })
        } else {
          //Execute redirection directly if not on webpage 3
          this.$store.commit('changeShowSelect',true)
          this.$router.push({name: 'main'})
        }
      },
      directToCitation(){ //Function to direct to webpage 4; logic is similar to the above funtion
        let currentPage = this.$store.getters.getCurrentPage
        console.log(currentPage)
        if(currentPage.match("manual")){
          this.$confirm("Are you sure to redirect to another page? Your information will not be preserved.", "Page Redirection", {
            confirmButtonText: "Yes",
            cancelButtonText: "Cancel",
            type: "warning"
          }).then(()=>{
            console.log("entered")
            this.$store.commit("setEmptyCitationFormat", this.$store.getters.getFormat)
            this.$store.commit('changeShowSelect',false)
            this.$router.push({name:'citation'})
          }).catch(()=>{
            this.$message({
              type: 'info',
              message: 'Redirection canceled.'
            })
          })
        } else {
          this.$store.commit('changeShowSelect',false)
          this.$router.push({name:'citation'})
        }
        
      },
      switchLanguage(){
        if(this.$root.$i18n.locale == 'en'){
          this.$root.$i18n.locale = 'cn'
          this.$store.commit('setCurrentLanguage','cn')
        } else {
          this.$root.$i18n.locale = 'en'
          this.$store.commit('setCurrentLanguage','en')
        }
      }
    },
    watch:{
      "isShow": function(val){ 
        //Monitor value of isShow and update the value of state whenver change is detected
        this.state = val
      }
    },
    i18n: {
      messages: {
        en: {
          'create': 'Create Citation',
          'my': 'My Citations',
          'switch': 'Switch Language'
        },
        cn: {
          'create': '',
          'my': '',
          'switch': ''
        }
      }
    }
  };
</script>

<style scoped> 
  @font-face {
    font-family: Roboto;
    src: url(./Roboto-Bold.ttf);
  }
  .bigContain{
    width: 50%;
