<template>
  <div class="manualContain">
    <div>
      <el-dropdown placement="bottom">
        <el-button round class="button">
          <img src="./arrow.svg" />
          <span>{{$t(this.$store.getters.getSelectedType)}}</span>
        </el-button>
        <el-dropdown-menu>
          <el-dropdown-item v-for="(type,index) in typeList" :key="index">
            <div style="width: 100%;" @click="selectType(type)" class="typeText">{{$t(type)}}</div>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
    <div class="inputContain">
      <!--For loop to iterate curFormat-->
      <div v-for="(spec,index) in curFormat" :key="index" style="width: 80%;">
        <!--Conditional to identify if element is dictionary (have sub citation item) or list (don't have)-->
        <div v-if="spec.length==undefined" class="rowContain">
          <div class="specTitle">
            {{$t(Object.keys(spec)[0])}}<!--Title of citation item-->
          </div>
          <div class="subContain">
            <!--Compute length of div block with function getFlex and iterate sub citation-->
            <div v-for="(item,index1) in spec[Object.keys(spec)[0]]"
            :key="index1" :style="getFlex(spec[Object.keys(spec)[0]].length)" 
            class="fieldContain">
              <div class="fieldText">
                {{$t(curFormat[index][Object.keys(spec)[0]][index1][0])}} <!--Title of sub citation item-->
              </div>
              <!--Show normal input text field if type of input reads 'normal'-->
              <el-input v-show="curFormat[index][Object.keys(spec)[0]][index1][1]=='normal'" class="formatField"
              v-model="curFormat[index][Object.keys(spec)[0]][index1][2]" clearable>
              </el-input>
              <!--Show drop-down menu if type of input reads 'dropdown'-->
              <el-select v-show="curFormat[index][Object.keys(spec)[0]][index1][1]=='dropdown'"
               v-model="curFormat[index][Object.keys(spec)[0]][index1][2]"
               clearable filterable placement="bottom" class="formatField">
                <el-option
                v-for="(item2,index2) in dropdowns[curFormat[index][Object.keys(spec)[0]][index1][0]]"
                :key="index2" :label="$t(item2.label)" :value="item2.value" class="dropdownText">
                  <span style="float: left">
                    <div v-if="curFormat[index][Object.keys(spec)[0]][index1][0] == 'Role'">{{$t(item2.value)}}</div>
                    <div else>{{item2.value}}</div>
                  </span>
                  <span style="float: right; padding-left: 2em;" v-show="curFormat[index][Object.keys(spec)[0]][index1][0] != 'Role'">{{$t(item2.label)}}</span>
                </el-option>
              </el-select>
              <!--Show date picker if type of input reads 'date'-->
              <el-date-picker v-show="curFormat[index][Object.keys(spec)[0]][index1][1]=='date'"
              v-model="curFormat[index][Object.keys(spec)[0]][index1][2]" type="date"
              placement="bottom" class="formatField">
              </el-date-picker>
            </div>
            <div style="display: flex; flex-direction:row">
              <img src="./add.svg" v-show="Object.keys(spec)[0] == 'Creator Information' " class="icons" @click="addCreator(index)">
              <img src="./del.svg" v-show="Object.keys(spec)[0] == 'Creator Information' " class="icons" @click="delCreator(index)">
              <img src="./add.svg" v-show="Object.keys(spec)[0] == 'Other Creator' " class="icons" @click="addOtherCreator(index)">
              <img src="./del.svg" v-show="Object.keys(spec)[0] == 'Other Creator' " class="icons" @click="delOtherCreator(index)">
              <img src="./add.svg" v-show="Object.keys(spec)[0] == 'Patent Applicant/Patentee' " class="icons" @click="addPatent(index)">
              <img src="./del.svg" v-show="Object.keys(spec)[0] == 'Patent Applicant/Patentee' " class="icons" @click="delPatent(index)">
            </div>
          </div>
        </div>
        <div v-else class="fieldContain">
          <div class="fieldText" style="font-size: 1.5em !important;">
            {{$t(spec[0])}}<!--Title of citation item-->
          </div>
          <!--Logic in L68-84 follows the same logic between L33-51-->
          <el-input v-show="spec[1]=='normal'" 
          class="formatField"
          v-model="curFormat[index][2]" clearable>
          </el-input>
          <el-select v-show="spec[1]=='dropdown'"
          filterable clearable 
          v-model="curFormat[index][2]" class="formatField" placement='bottom'>
            <el-option v-for="(item2,index2) in dropdowns[spec[0]]" :key="index2"
            :value="item2.label" :label="$t(item2.value)">
              <span style="float: left">{{$t(item2.value)}}</span>
              <span style="float: right; padding-left: 2em;" v-show="typeof item2.label != 'number'">{{$t(item2.label)}}</span>
            </el-option>
          </el-select>
          <el-date-picker v-show="spec[1]=='date'"
          v-model="curFormat[index][2]" type="date"
           class="formatField" placement='bottom'>
          </el-date-picker>
        </div>
      </div>
    </div>
    <div>
      <el-button round class="button" @click="directToCitation">
        <span>{{$t('create')}}</span>
      </el-button>
    </div>
  </div>
</template>

<script>
  //Import pop-up menu component
  export default{
    name: "vmanual",
    data(){
      return{
        typeList: this.$store.getters.getType, //initialize list storing available source types
        selectedType: "", //Initialize empty string to store selected srouce type
        curFormat: this.$store.getters.getFormat, //Initialize citation format for current source type to generate input field GUI and store user input
        dropdowns: this.$store.getters.getFormatDropdown, //Initialize options for dropdown menus
      }
    },
    methods:{
      selectType(val){ //Function called when user selected a new source type from drop-down menu
        if(val != this.$store.getters.getSelectedType){ //Avoid repetitive operation if the newly selected source type is the current selected source type
          this.$store.commit("selectType",val) //Update selected source type in central store
          this.curFormat = this.$store.getters.getFormat //Update citation format
        }
      },
      getFlex(val){ //Function to compute flex length (percentage) for each input field
        var percent = (100/val).toString()+"%"
        var flex = "flex: 0 0 "+percent+";"
        return flex
      },
      addCreator(index){ //Function to add creator information when add button clicked
        this.curFormat.splice(index+1,0,{"Creator Information": [['Role','dropdown',''],['First Name','normal',''],['Last Name','normal','']]})
      },
      directToCitation(){ //Function to redirect to webpage 4 when "Create Citation" is clicked
        this.$store.dispatch('processRawCitationData',this.curFormat) //process current user input to formatted citation before redirection 
        this.$store.commit("setEmptyCitationFormat",this.curFormat) 
        this.$emit('directToPage','citation') //Emit directToPage event to father component (typePage.vue) with parameter 'citation' (redirection to component with name 'citation')
      },
      delCreator(index){ //Function to delete creator information
        if(index!=0 || Object.keys(this.curFormat[index+1])[0]=="Creator Information"){
          this.curFormat.splice(index,1) //Delete if current creator number > 1
        } else {
          this.$message({ //Warn user if current creator number = 1
            message: "You must have at least one creator",
            type: "warning"
          })
        }
      },
      addOtherCreator(index){ //Add "Other Creator Information"
        this.curFormat.splice(index+1,0,{"Other Creator": [['Role','dropdown',''],['First Name','normal',''],['Last Name','normal','']]})
      },
      delOtherCreator(index){ //Delete "Other Creator Information"
        this.curFormat.splice(index,1)
      },
      addPatent(index){ //Add patent applicant
        this.curFormat.splice(index+1,0,{"Patent Applicant/Patentee": [['First Name','normal',''],['Last Name','normal','']]})
      },
      delPatent(index){ //Delete patent application
        if(index!=0 || Object.keys(this.curFormat[index+1])[0]=="Patent Applicant/Patentee"){
          this.curFormat.splice(index,1)
        } else {
          this.$message({ //Warn user if current patent applicant number = 1
            message:'You must have at least one patent applicant/patentee',
            type: 'Warning'
          })
        }
      }
    },
    mounted: function(){
      this.$store.commit('setCurrentPage','manual')
    },
    destroyed: function(){
      this.$store.commit('setCurrentPage','')
    },
    i18n: {
      messages: {
        en: {
          'Monograph': 'Monograph',
          'Contribution Literature': 'Contribution Literature',
          'Serial': 'Serial',
          'Electronic Resource': 'Electronic Resource',
